<template>
  <div class="about">
    <div>
      <img :src="data.cover" class="img" />
    </div>
    <div class="tips">
      <div>
        <span style="font-size:24px;line-height:60px">{{data.name}}</span>
      </div>
      <div class="tips_box">{{data.introduce}}</div>
      <div>
        <van-row type="flex" justify="space-between" class="msg">
          <van-col span="8">
            <span>作者:{{data.author}}</span>
          </van-col>
          <van-col span="8">
            <span>时间:{{data.time}}</span>
          </van-col>
          <van-col span="8">
            <span class="txtRight">类型:{{data.tag}}</span>
          </van-col>
        </van-row>
      </div>
    </div>
    <div>
      <van-row>
        <van-col span="6" v-for="(item,i) in list" :key="i">
          <div class="list_box">
            <router-link :to="{path:'/detail',query:{url:item.url}}" class="span">{{item.num}}</router-link>
          </div>
        </van-col>
      </van-row>
    </div>
  </div>
</template>

<script>
import { mhListApi } from "@/api/api";
export default {
  name: "",
  data() {
    return {
      data: {},
      list: []
    };
  },
  methods: {},
  created() {
    let { url } = this.$route.query;
    mhListApi(url).then(res => {
      let { data, list, code } = res.data;
      if (code === 0) {
        this.data = data;
        this.list = list;
      }
    });
  }
};
</script>

<style lang='less' scoped>
@imgHeight: 250px;

.color,
.about {
  color: "#333";
}
.img {
  width: 100%;
  height: @imgHeight;
}
.tips {
  padding-left: 10px;
  padding-right: 10px;
  margin-bottom: 20px;
}
.tips .tips_box {
  text-indent: 2em;
  font-size: 14px;
  margin-bottom: 10px;
  height: 76px;
  overflow: hidden;
  text-overflow: ellipsis;
}
.msg span {
  display: inline-block;
  width: 100%;
  line-height: 20px;
  font-size: 14px;
  color: #333;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.msg > span:nth-of-type(2) {
  text-align: center;
}
.msg .txtRight {
  text-align: right;
}
.span {
  display: inline-block;
  width: 100%;
  height: 30px;
  line-height: 30px;
  text-align: center;
  font-size: 14px;
  color: #323233;
  background-color: #fff;
  border: 1px solid #ebedf0;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.list_box {
  padding: 10px;
  box-sizing: border-box;
}
</style>